
 /**
  * Global styles
  */
 p.demo_store,
 .woocommerce-store-notice {
     position: absolute;
     top: 0;
     left: 0;
     right: 0;
     margin: 0;
     width: 100%;
     font-size: 1em;
     padding: 1em 0;
     text-align: center;
     z-index: 99998;
     box-shadow: 0 1px 1em rgba(0, 0, 0, 0.2);
     display: none;
 
     a {
         text-decoration: underline;
     }
 }
 
 .screen-reader-text {
     clip: rect(1px, 1px, 1px, 1px);
     height: 1px;
     overflow: hidden;
     position: absolute !important;
     width: 1px;
     word-wrap: normal !important;
 }
 
 .admin-bar p.demo_store {
     top: 32px;
 }
 
 /**
  * Utility classes
  */
 .clear {
     clear: both;
 }
 
 /**
  * Main WooCommerce styles
  */
 .woocommerce {
     .blockUI.blockOverlay {
         position: relative;
         
     }
 
 
     a.remove {
         display: block;
         font-size: 1.5em;
         height: 1em;
         width: 1em;
         text-align: center;
         line-height: 1;
         border-radius: 100%;
         color: red !important; // Required for default theme compatibility
         text-decoration: none;
         font-weight: 700;
         border: 0;
 
         &:hover {
             color: #fff !important; // Required for default theme compatibility
             background: red;
         }
     }
 
     small.note {
         display: block;
         color: $subtext;
         font-size: 0.857em;
         margin-top: 10px;
     }
 
     .woocommerce-breadcrumb {
         @include clearfix();
         margin: 0 0 1em;
         padding: 0;
         font-size: 0.92em;
         color: $subtext;
     }
 
     .quantity .qty {
         width: 3.631em;
         text-align: center;
     }
 
     /**
      * Product Page
      */
     div.product {
         margin-bottom: 0;
         position: relative;
 
         .product_title {
             clear: none;
             margin-top: 0;
             padding: 0;
         }
 
         span.price,
         p.price {
             font-size: 1.25em;
 
             ins {
                 background: inherit;
                 font-weight: 700;
                 display: inline-block;
             }
 
             del {
                 opacity: 0.5;
                 display: inline-block;
             }
         }
 
         p.stock {
             font-size: 0.92em;
         }
 
         .stock {
             color:green;
         }
 
         .out-of-stock {
             color: red;
         }
 
         .woocommerce-product-rating {
             margin-bottom: 1.618em;
         }
 
         div.images {
             margin-bottom: 2em;
 
             img {
                 display: block;
                 width: 100%;
                 height: auto;
                 box-shadow: none;
             }
 
             div.thumbnails {
                 padding-top: 1em;
             }
 
             &.woocommerce-product-gallery {
                 position: relative;
             }
 
             .woocommerce-product-gallery__wrapper {
                 transition: all cubic-bezier(0.795, -0.035, 0.000, 1.000) .5s;
                 margin: 0;
                 padding: 0;
             }
 
             .woocommerce-product-gallery__wrapper .zoomImg {
                 background-color: #fff;
                 opacity: 0;
             }
 
             .woocommerce-product-gallery__image:nth-child(n+2) {
                 width: 25%;
                 display: inline-block;
             }
 
             .woocommerce-product-gallery__trigger {
                 position: absolute;
                 top: .5em;
                 right: .5em;
                 font-size: 2em;
                 z-index: 9;
                 width: 36px;
                 height: 36px;
                 background: #fff;
                 text-indent: -9999px;
                 border-radius: 100%;
                 box-sizing: content-box;
 
                 &:before {
                     content: "";
                     display: block;
                     width: 10px;
                     height: 10px;
                     border: 2px solid #000;
                     border-radius: 100%;
                     position: absolute;
                     top: 9px;
                     left: 9px;
                     box-sizing: content-box;
                 }
 
                 &:after {
                     content: "";
                     display: block;
                     width: 2px;
                     height: 8px;
                     background: #000;
                     border-radius: 6px;
                     position: absolute;
                     top: 19px;
                     left: 22px;
                     transform: rotate(-45deg);
                     box-sizing: content-box;
                 }
             }
 
             .flex-control-thumbs {
                 overflow: hidden;
                 zoom: 1;
                 margin: 0;
                 padding: 0;
 
                 li {
                     width: 25%;
                     float: left;
                     margin: 0;
                     list-style: none;
 
                     img {
                         cursor: pointer;
                         opacity: .5;
                         margin: 0;
 
                         &.flex-active,
                         &:hover {
                             opacity: 1;
                         }
                     }
                 }
             }
         }
 
         .woocommerce-product-gallery--columns-3 {
             .flex-control-thumbs li:nth-child(3n+1) {
                 clear: left;
             }
         }
 
         .woocommerce-product-gallery--columns-4 {
             .flex-control-thumbs li:nth-child(4n+1) {
                 clear: left;
             }
         }
 
         .woocommerce-product-gallery--columns-5 {
             .flex-control-thumbs li:nth-child(5n+1) {
                 clear: left;
             }
         }
 
         div.summary {
             margin-bottom: 2em;
         }
 
         div.social {
             text-align: right;
             margin: 0 0 1em;
 
             span {
                 margin: 0 0 0 2px;
 
                 span {
                     margin: 0;
                 }
 
                 .stButton .chicklets {
                     padding-left: 16px;
                     width: 0;
                 }
             }
 
             iframe {
                 float: left;
                 margin-top: 3px;
             }
         }
 
         .woocommerce-tabs {
             ul.tabs {
                 list-style: none;
                 padding: 0 0 0 1em;
                 margin: 0 0 1.618em;
                 overflow: hidden;
                 position: relative;
 
                 li {
                     display: inline-block;
                     position: relative;
                     z-index: 0;
                     border-radius: 4px 4px 0 0;
                     margin: 0 -5px;
                     padding: 0 1em;
 
                     a {
                         display: inline-block;
                         padding: 0.5em 0;
                         font-weight: 700;
                         text-decoration: none;
 
                         &:hover {
                             text-decoration: none;
                         }
                     }
 
                     &.active {
                         z-index: 2;
 
                         a {
                             color: inherit;
                             text-shadow: inherit;
                         }
                     }
 
                     &::before,
                     &::after {
                         position: absolute;
                         bottom: -1px;
                         width: 5px;
                         height: 5px;
                         content: ' ';
                         box-sizing: border-box;
                     }
 
                     &::before {
                         left: -5px;
                         border-bottom-right-radius: 4px;
                         border-width: 0 1px 1px 0;
                     }
 
                     &::after {
                         right: -5px;
                         border-bottom-left-radius: 4px;
                         border-width: 0 0 1px 1px;
                     }
                 }
 
                 &::before {
                     position: absolute;
                     content: ' ';
                     width: 100%;
                     bottom: 0;
                     left: 0;
                     z-index: 1;
                 }
             }
 
             .panel {
                 margin: 0 0 2em;
                 padding: 0;
             }
         }
 
         p.cart {
             margin-bottom: 2em;
             @include clearfix();
         }
 
         form.cart {
             margin-bottom: 2em;
             @include clearfix();
 
             div.quantity {
                 float: left;
                 margin: 0 4px 0 0;
             }
 
             table {
                 border-width: 0 0 1px;
 
                 td {
                     padding-left: 0;
                 }
 
                 div.quantity {
                     float: none;
                     margin: 0;
                 }
 
                 small.stock {
                     display: block;
                     float: none;
                 }
             }
 
             .variations {
                 margin-bottom: 1em;
                 border: 0;
                 width: 100%;
 
                 td,
                 th {
                     border: 0;
                     vertical-align: top;
                     line-height: 2em;
                 }
 
                 label {
                     font-weight: 700;
                 }
 
                 select {
                     max-width: 100%;
                     min-width: 75%;
                     display: inline-block;
                     margin-right: 1em;
                 }
 
                 td.label {
                     padding-right: 1em;
                 }
             }
 
             .woocommerce-variation-description p {
                 margin-bottom: 1em;
             }
 
             .reset_variations {
                 visibility: hidden;
                 font-size: 0.83em;
             }
 
             .wc-no-matching-variations {
                 display: none;
             }
 
             .button {
                 vertical-align: middle;
                 float: left;
             }
 
             .group_table {
                 td.woocommerce-grouped-product-list-item__label {
                     padding-right: 1em;
                     padding-left: 1em;
                 }
 
                 td {
                     vertical-align: top;
                     padding-bottom: 0.5em;
                     border: 0;
                 }
 
                 td:first-child {
                     width: 4em;
                     text-align: center;
                 }
 
                 .wc-grouped-product-add-to-cart-checkbox {
                     display: inline-block;
                     width: auto;
                     margin: 0 auto;
                     transform:scale(1.5, 1.5);
                 }
             }
         }
     }
 
     span.onsale {
         min-height: 3.236em;
         min-width: 3.236em;
         padding: 0.202em;
         font-size: 1em;
         font-weight: 700;
         position: absolute;
         text-align: center;
         line-height: 3.236;
         top: -0.5em;
         left: -0.5em;
         margin: 0;
         border-radius: 100%;
         font-size: 0.857em;
         z-index: 9;
     }
 
     /**
      * Product loops
      */
     .products ul,
     ul.products {
         margin: 0 0 1em;
         padding: 0;
         list-style: none outside;
         clear: both;
         @include clearfix();
 
         li {
             list-style: none outside;
         }
     }
 
     ul.products li.product {
         .onsale {
             top: 0;
             right: 0;
             left: auto;
             margin: -0.5em -0.5em 0 0;
         }
 
         h3,
         .woocommerce-loop-product__title,
         .woocommerce-loop-category__title {
             padding: 0.5em 0;
             margin: 0;
             font-size: 1em;
         }
 
         a {
             text-decoration: none;
         }
 
         a img {
             width: 100%;
             height: auto;
             display: block;
             margin: 0 0 1em;
             box-shadow: none;
         }
 
         strong {
             display: block;
         }
 
         .star-rating {
             font-size: 0.857em;
         }
 
         .button {
             margin-top: 1em;
         }
 
         .price {
             display: block;
             font-weight: normal;
             margin-bottom: 0.5em;
             font-size: 0.857em;
 
             del {
                 color: inherit;
                 opacity: 0.5;
                 display: inline-block;
             }
 
             ins {
                 background: none;
                 font-weight: 700;
                 display: inline-block;
             }
 
             .from {
                 font-size: 0.67em;
                 margin: -2px 0 0 0;
                 text-transform: uppercase;
             }
         }
     }
 
     .woocommerce-result-count {
         margin: 0 0 1em;
     }
 
     .woocommerce-ordering {
         margin: 0 0 1em;
 
         select {
             vertical-align: top;
         }
     }
 
     nav.woocommerce-pagination {
         text-align: center;
 
         ul {
             display: inline-block;
             white-space: nowrap;
             padding: 0;
             clear: both;
             border-right: 0;
             margin: 1px;
 
             li {
                 padding: 0;
                 margin: 0;
                 display: inline;
                 overflow: hidden;
 
                 a,
                 span {
                     margin: 0;
                     text-decoration: none;
                     padding: 0;
                     line-height: 1;
                     font-size: 1em;
                     font-weight: normal;
                     padding: 0.5em;
                     min-width: 1em;
                     display: block;
                 }
             }
         }
     }
 
     /**
      * Buttons
      */
     a.button,
     button.button,
     input.button,
     #respond input#submit {
         font-size: 100%;
         margin: 0;
         line-height: 1;
         cursor: pointer;
         position: relative;
         text-decoration: none;
         overflow: visible;
         padding: 0.618em 1em;
         font-weight: 700;
         border-radius: 3px;
         left: auto;
         border: 0;
         display: inline-block;
         background-image: none;
         box-shadow: none;
         text-shadow: none;
 
         &.loading {
             opacity: 0.25;
             padding-right: 2.618em;
 
             &::after {
                 content: '\e01c';
                 vertical-align: top;
                 font-weight: 400;
                 position: absolute;
                 top: 0.618em;
                 right: 1em;
                 animation: spin 2s linear infinite;
             }
         }
 
         &.added::after {
             content: '\e017';
             margin-left: 0.53em;
             vertical-align: bottom;
         }
 
         &:hover {
             background-color: $color-black-secondary;
             text-decoration: none;
             background-image: none;
             color:$color-white;
         }
 
         &.alt {

             -webkit-font-smoothing: antialiased;
         }
 
     }
 
     .cart .button,
     .cart input.button {
         float: none;
     }
 
     a.added_to_cart {
         padding-top: 0.5em;
         display: inline-block;
     }
 
     /**
      * Reviews
      */
     #reviews {
         h2 small {
             float: right;
             color: $subtext;
             font-size: 15px;
             margin: 10px 0 0;
 
             a {
                 text-decoration: none;
                 color: $subtext;
             }
         }
 
         h3 {
             margin: 0;
         }
 
         #respond {
             margin: 0;
             border: 0;
             padding: 0;
         }
 
         #comment {
             height: 75px;
         }
 
         #comments {
             .add_review {
                 @include clearfix();
             }
 
             h2 {
                 clear: none;
             }
 
             ol.commentlist {
                 @include clearfix();
                 margin: 0;
                 width: 100%;
                 background: none;
                 list-style: none;
 
                 li {
                     padding: 0;
                     margin: 0 0 20px;
                     border: 0;
                     position: relative;
                     background: 0;
                     border: 0;
 
                     .meta {
                         color: $subtext;
                         font-size: 0.75em;
                     }
 
                     img.avatar {
                         float: left;
                         position: absolute;
                         top: 0;
                         left: 0;
                         padding: 3px;
                         width: 32px;
                         height: auto;
                         margin: 0;
                         box-shadow: none;
                     }
 
                     .comment-text {
                         margin: 0 0 0 50px;
                         border-radius: 4px;
                         padding: 1em 1em 0;
                         @include clearfix();
 
                         p {
                             margin: 0 0 1em;
                         }
 
                         p.meta {
                             font-size: 0.83em;
                         }
                     }
                 }
 
                 ul.children {
                     list-style: none outside;
                     margin: 20px 0 0 50px;
 
                     .star-rating {
                         display: none;
                     }
                 }
 
                 #respond {
                     border-radius: 4px;
                     padding: 1em 1em 0;
                     margin: 20px 0 0 50px;
                 }
             }
 
             .commentlist > li::before {
                 content: '';
             }
         }
     }
 
     /**
      * Star ratings
      */
     .star-rating {
         float: right;
         overflow: hidden;
         position: relative;
         height: 1em;
         line-height: 1;
         font-size: 1em;
         width: 5.4em;
         font-family: 'star';
 
         &::before {
             content: '\73\73\73\73\73';
             float: left;
             top: 0;
             left: 0;
             position: absolute;
         }
 
         span {
             overflow: hidden;
             float: left;
             top: 0;
             left: 0;
             position: absolute;
             padding-top: 1.5em;
         }
 
         span::before {
             content: '\53\53\53\53\53';
             top: 0;
             position: absolute;
             left: 0;
         }
     }
 
     .woocommerce-product-rating {
         @include clearfix();
         line-height: 2;
         display: block;
 
         .star-rating {
             margin: 0.5em 4px 0 0;
             float: left;
         }
     }
 
     .products .star-rating {
         display: block;
         margin: 0 0 0.5em;
         float: none;
     }
 
     .hreview-aggregate .star-rating {
         margin: 10px 0 0;
     }
 
     #review_form #respond {
         @include clearfix();
         position: static;
         margin: 0;
         width: auto;
         padding: 0;
         background: transparent none;
         border: 0;
 
         p {
             margin: 0 0 10px;
         }
 
         .form-submit input {
             left: auto;
         }
 
         textarea {
             box-sizing: border-box;
             width: 100%;
         }
     }
 
     p.stars {
         a {
             position: relative;
             height: 1em;
             width: 1em;
             text-indent: -999em;
             display: inline-block;
             text-decoration: none;
 
             &::before {
                 display: block;
                 position: absolute;
                 top: 0;
                 left: 0;
                 width: 1em;
                 height: 1em;
                 line-height: 1;
                 font-family: 'WooCommerce';
                 content: '\e021';
                 text-indent: 0;
             }
 
             &:hover ~ a::before {
                 content: '\e021';
             }
         }
 
         &:hover a::before {
             content: '\e020';
         }
 
         &.selected {
             a.active {
                 &::before {
                     content: '\e020';
                 }
 
                 ~ a::before {
                     content: '\e021';
                 }
             }
 
             a:not( .active )::before {
                 content: '\e020';
             }
         }
     }
 
     /**
      * Tables
      */
     table.shop_attributes {
         border: 0;
         border-top: 1px dotted rgba(0, 0, 0, 0.1);
         margin-bottom: 1.618em;
         width: 100%;
 
         th {
             width: 150px;
             font-weight: 700;
             padding: 8px;
             border-top: 0;
             border-bottom: 1px dotted rgba(0, 0, 0, 0.1);
             margin: 0;
             line-height: 1.5;
         }
 
         td {
             font-style: italic;
             padding: 0;
             border-top: 0;
             border-bottom: 1px dotted rgba(0, 0, 0, 0.1);
             margin: 0;
             line-height: 1.5;
 
             p {
                 margin: 0;
                 padding: 8px 0;
             }
         }
 
         tr:nth-child(even) td,
         tr:nth-child(even) th {
             background: rgba(0, 0, 0, 0.025);
         }
     }
 
     table.shop_table {
         border: 1px solid rgba(0, 0, 0, 0.1);
         margin: 0 -1px 24px 0;
         text-align: left;
         width: 100%;
         border-collapse: separate;
         border-radius: 5px;
 
         th {
             font-weight: 700;
             padding: 9px 12px;
         }
 
         td {
             border-top: 1px solid rgba(0, 0, 0, 0.1);
             padding: 6px 12px;
             vertical-align: middle;
 
             small {
                 font-weight: normal;
             }
         }
 
         tbody:first-child tr:first-child {
             th,
             td {
                 border-top: 0;
             }
         }
 
         tfoot td,
         tfoot th,
         tbody th {
             font-weight: 700;
             border-top: 1px solid rgba(0, 0, 0, 0.1);
         }
     }
 
     table.my_account_orders {
         font-size: 0.85em;
 
         th,
         td {
             padding: 4px 8px;
             vertical-align: middle;
         }
 
         .button {
             white-space: nowrap;
         }
 
         .order-actions {
             text-align: right;
 
             .button {
                 margin: 0.125em 0 0.125em 0.25em;
             }
         }
     }
 
     table.woocommerce-MyAccount-downloads {
         td,
         th {
             vertical-align: top;
             text-align: center;
 
             &:first-child {
                 text-align: left;
             }
             &:last-child {
                 text-align: left;
             }
             .woocommerce-MyAccount-downloads-file::before {
                 content: '\2193';
                 display: inline-block;
             }
         }
     }
 
     td.product-name {
         dl.variation, .wc-item-meta {
             list-style: none outside;
 
             dt, .wc-item-meta-label {
                 clear: both;
                 margin-right: .25em;
                 display: inline-block;
                 list-style: none outside;
             }
 
             dd {
                 margin: 0;
             }
 
             p,
             &:last-child {
                 margin-bottom: 0;
             }
         }
 
         p.backorder_notification {
             font-size: 0.83em;
         }
     }
 
     td.product-quantity {
         min-width: 80px;
     }
 
     /**
      * Cart sidebar
      */
     ul.cart_list,
     ul.product_list_widget {
         list-style: none outside;
         padding: 0;
         margin: 0;
 
         li {
             padding: 4px 0;
             margin: 0;
             @include clearfix();
             list-style: none;
 
             a {
                 display: block;
                 font-weight: 700;
             }
 
             img {
                 float: right;
                 margin-left: 4px;
                 width: 32px;
                 height: auto;
                 box-shadow: none;
             }
 
             dl {
                 margin: 0;
                 padding-left: 1em;
                 border-left: 2px solid rgba(0, 0, 0, 0.1);
                 @include clearfix();
 
                 dt,
                 dd {
                     display: inline-block;
                     margin-bottom: 1em;
                 }
 
                 dt {
                     font-weight: 700;
                     padding: 0 0 0.25em;
                     margin: 0 4px 0 0;
                     clear: left;
                 }
 
                 dd {
                     padding: 0 0 0.25em;
 
                     p:last-child {
                         margin-bottom: 0;
                     }
                 }
             }
 
             .star-rating {
                 float: none;
             }
         }
     }
 
     &.widget_shopping_cart,
     .widget_shopping_cart {
         .total {
             padding: 4px 0 0;
 
             strong {
                 min-width: 40px;
                 display: inline-block;
             }
         }
 
         .cart_list li {
             padding-left: 2em;
             position: relative;
             padding-top: 0;
 
             a.remove {
                 position: absolute;
                 top: 0;
                 left: 0;
             }
         }
 
         .buttons {
             @include clearfix();
             a {
                 margin-right: 5px;
                 margin-bottom: 5px;
             }
         }
     }
 
     /**
      * Forms
      */
     form .form-row {
         padding: 3px;
         margin: 0 0 6px;
 
         [placeholder]:focus::-webkit-input-placeholder {
             transition: opacity 0.5s 0.5s ease;
             opacity: 0;
         }
 
         label {
             line-height: 2;
         }
 
         label.hidden {
             visibility: hidden;
         }
 
         label.inline {
             display: inline;
         }
 
         .woocommerce-input-wrapper {
             .description {
                 background: #1e85be;
                 color: #fff;
                 border-radius: 3px;
                 padding: 1em;
                 margin: .5em 0 0;
                 clear: both;
                 display: none;
                 position: relative;
 
                 a {
                     color: #fff;
                     text-decoration: underline;
                     border: 0;
                     box-shadow: none;
                 }
 
                 &:before {
                     left: 50%;
                     top: 0%;
                     margin-top: -4px;
                     transform: translatex(-50%) rotate(180deg);
                     content: "";
                     position: absolute;
                     border-width: 4px 6px 0 6px;
                     border-style: solid;
                     border-color: #1e85be transparent transparent transparent;
                     z-index: 100;
                     display: block;
                 }
             }
         }
 
         select {
             cursor: pointer;
             margin: 0;
         }
 
         .required {
             color: red;
             font-weight: 700;
             border: 0 !important;
             text-decoration: none;
             visibility: hidden; // Only show optional by default.
         }
 
         .optional {
             visibility: visible;
          }
 
         .input-checkbox {
             display: inline;
             margin: -2px 8px 0 0;
             text-align: center;
             vertical-align: middle;
         }
 
         input.input-text,
         textarea {
             box-sizing: border-box;
             width: 100%;
             margin: 0;
             outline: 0;
             line-height: normal;
         }
 
         textarea {
             height: 4em;
             line-height: 1.5;
             display: block;
             box-shadow: none;
         }
 
         .select2-container {
             width: 100%;
             line-height: 2em;
         }
 
         &.woocommerce-invalid {
             label {
                 color: $red;
             }
             .select2-container,
             input.input-text,
             select {
                 border-color: $red;
             }
         }
 
         &.woocommerce-validated {
             .select2-container,
             input.input-text,
             select {
                 border-color: $green;
             }
         }
 
         ::-webkit-input-placeholder {
             line-height: normal;
         }
 
         :-moz-placeholder {
             line-height: normal;
         }
 
         :-ms-input-placeholder {
             line-height: normal;
         }
     }
 
     form.login,
     form.checkout_coupon,
