         li {
             float: left;
             margin-right: 2em;
             text-transform: uppercase;
             font-size: 0.715em;
             line-height: 1;
             padding-right: 2em;
             margin-left: 0;
             padding-left: 0;
             list-style-type: none;
 
             strong {
                 display: block;
