         }
     }
 
     .wc-bacs-bank-details-account-name {
         font-weight: bold;
     }
 
     .woocommerce-order-downloads,
     .woocommerce-customer-details,
     .woocommerce-order-details {
         margin-bottom: 2em;
 
         *:last-child {
             margin-bottom: 0;
         }
     }
     .woocommerce-customer-details {
         address {
             font-style: normal;
             margin-bottom: 0;
             border: 1px solid rgba(0, 0, 0, 0.1);
             border-bottom-width: 2px;
             border-right-width: 2px;
             text-align: left;
             width: 100%;
             border-radius: 5px;
             padding: 6px 12px;
         }
         .woocommerce-customer-details--phone,
         .woocommerce-customer-details--email {
             margin-bottom: 0;
             padding-left: 1.5em;
         }
         .woocommerce-customer-details--phone::before {
             content: "tel-icon";
             margin-left: -1.5em;
             line-height: 1.75;
             position: absolute;
         }
         .woocommerce-customer-details--email::before {
            content: "tel-icon-bef";
             margin-left: -1.5em;
             line-height: 1.75;
             position: absolute;
         }
     }
 
     /**
      * Layered nav widget
      */
     .woocommerce-widget-layered-nav-list {
         margin: 0;
         padding: 0;
         border: 0;
         list-style: none outside;
 
         .woocommerce-widget-layered-nav-list__item {
             @include clearfix();
             padding: 0 0 1px;
             list-style: none;
 
             a,
             span {
                 padding: 1px 0;
             }
         }
         .woocommerce-widget-layered-nav-list__item--chosen a::before {
            content:"icon--";
             color: $red;
         }
     }
     .woocommerce-widget-layered-nav-dropdown__submit {
         margin-top: 1em;
     }
 
     .widget_layered_nav_filters ul {
         margin: 0;
         padding: 0;
         border: 0;
         list-style: none outside;
         overflow: hidden;
         zoom: 1;
 
         li {
             float: left;
             padding: 0 1em 1px 1px;
             list-style: none;
 
             a {
                 text-decoration: none;
 
                 &::before {
                     color: $red;
                     vertical-align: inherit;
                     margin-right: .5em;
                 }
             }
         }
     }
 
     /**
      * Price filter widget
      */
     .widget_price_filter {
         .price_slider {
             margin-bottom: 1em;
         }
 
         .price_slider_amount {
             text-align: right;
             line-height: 2.4;
             font-size: 0.8751em;
 
             .button {
                 font-size: 1.15em;
                 float: left;
             }
         }
 
         .ui-slider {
             position: relative;
             text-align: left;
             margin-left: 0.5em;
             margin-right: 0.5em;
         }
 
         .ui-slider .ui-slider-handle {
             position: absolute;
             z-index: 2;
             width: 1em;
             height: 1em;
             border-radius: 1em;
             cursor: ew-resize;
             outline: none;
             top: -0.3em;
             /* rtl:ignore */
             margin-left: -0.5em;
         }
 
         .ui-slider .ui-slider-range {
             position: absolute;
             z-index: 1;
             font-size: 0.7em;
             display: block;
             border: 0;
             border-radius: 1em;
         }
 
         .price_slider_wrapper .ui-widget-content {
             border-radius: 1em;
             border: 0;
         }
 
         .ui-slider-horizontal {
             height: 0.5em;
         }
 
         .ui-slider-horizontal .ui-slider-range {
             top: 0;
             height: 100%;
         }
 
         .ui-slider-horizontal .ui-slider-range-min {
             left: -1px;
         }
 
         .ui-slider-horizontal .ui-slider-range-max {
             right: -1px;
         }
     }
 
     /**
      * Rating Filter Widget
      */
     .widget_rating_filter ul {
         margin: 0;
         padding: 0;
         border: 0;
         list-style: none outside;
 
         li {
             @include clearfix();
             padding: 0 0 1px;
             list-style: none;
 
             a {
                 padding: 1px 0;
                 text-decoration: none;
             }
 
             .star-rating {
                 float: none;
                 display: inline-block;
             }
         }
 
         li.chosen a::before {
            content:"iconEx";
            color: $red;
         }
     }
