

/* Globals
-------------------------------*/
*{
    margin: 0;padding:0;
}
html{
    font-size:$base-font-size;
    line-height: 1rem;
}
body{
    font-family:$font-stack;
    min-height:100%;
    font-size:$font-size-default;
    background-color:$color-background;
    -webkit-font-smoothing: antialiased;
    background:$color-background;
    width:$full;
}




/* Main settings
-------------------------------*/

/* float clearing for IE6 */
* html .clearfix{
 height: 1%;
 overflow: visible;
}

/* float clearing for IE7 */
*+html .clearfix{
 min-height: 1%;
}

/* float clearing for everyone else */
.clearfix:after{
 clear: both;
 content: ".";
 display: block;
 height: 0;
 visibility: hidden;
 font-size: 0;
}

.clr{clear:both;}


.colfull{width:$full;height:$full;padding:6rem;position:relative;box-sizing:border-box;}

.left{float:left;}
.right{float:right;}
.half{position:relative;width:$half;min-height:1px;}
.quarter{width:$quarter;}
.threequarter{width:percentage(3/4);}
.third{width:$third;}
.block{display:block;}
.auto{margin:auto;}
.hidden{display:none;}
.padding{padding:1rem;box-sizing:border-box;}
.paddingleft{padding-left:1rem;box-sizing:border-box;}
.paddingright{padding-right:1rem;box-sizing:border-box;}
.paddingtop{padding-top:1rem;box-sizing:border-box;}
.paddingbottom{padding-bottom:1rem;box-sizing:border-box;}
.small-paddingleft{padding-left:0.5rem;box-sizing:border-box;}
.small-paddingright{padding-right:0.5rem;box-sizing:border-box;}
.txtright{text-align:right;}
.txtcenter{text-align:center;}
.highlight{color:$color-highlight;}
.hightlight:hover{text-decoration:underline;}
.strong{font-weight:bold;}



/* Text
-------------------------------*/
h1, h2, h3, h4, h5, h6{font-weight: lighter;}
h1{font-size:rem(40px);margin-bottom:1.5rem}
h2{
    font-size:rem(25px);
    margin-bottom:1.5rem;

}
h3{font-size:1.75rem;margin-bottom:1.5rem;}
h4{font-size:rem(20px);line-height:28px;margin-bottom:$margin-bottom}
h5{font-size:1rem;}
h6{font-size:0.75rem;}
li{list-style-type: none;line-height: 30px;}
p{
    
    font-family: 'Open Sans', sans-serif;
    margin-bottom:$margin-bottom;
    line-height:1.5rem;
    font-size:1rem;
}
a{
    text-decoration:none;
    font-family:$font-stack;
    color:$color-highlight;
}
table,label,input,textarea{
    font-family:$font-stack;
    font-size:$font-size-default;
    padding:1em;
    border:solid 0px;
}


/* Buttons
-------------------------------*/
%btn{
    position:relative;
    cursor:pointer;
    display:inline-block;
    background-color:$color-btn;
    color:$color-black; 
    box-sizing:border-box;
    transition:all 0.15s ease-in 0s;
    border:none;
    font-size:1rem;
    padding:1rem 2rem;
    padding-right:2rem; 
    
    svg{
        height:1rem;
        position: absolute;
        right: 2rem;
        top: 50%;
        transform: translateY(-50%);
        -webkit-transition: right 0.2s ease-in 0s;
        transition: right 0.2s ease-in 0s;
        fill:$color-black;

        :hover{
            right:1rem;
            text-decoration:none;
            
        }    
    }
    &:hover{
        background-color:$color-black;
        color:#fff;
        svg{
            fill:#fff;
            right:1rem;
            text-decoration:none;
        }
    }
}
.btn,.button,.wpcf7-submit{
    @extend %btn;
}


.btn-large{
    @extend %btn;
    
    display:block;
    width:50%;
    float:left;
    font-size:1.33333rem;
    padding:1.5em 1.5em;
    box-sizing:border-box;

    &:nth-child(2){
        background-color:$color-white;
    }
    &:hover{
        background-color:$color-black;
    }
    svg{
        height:1.33333rem;
    }
}













input.btn,input.button{
    padding:1rem 2rem;
}
#posts{
    margin-top:2.5rem;

    > article.colfull{padding:6rem 12rem;}
}